import styled from "styled-components";

export const Picture = styled.img`
    position: relative;
    width: 280px;
    height: 170px;
    margin-top: 10px;
    margin-left: 10px;
    gap: 0px;
    background-color: #60a1a1;
    border-radius: 10px;
    opacity: 0px;
    bottom: 20px;
`